        testimonial: "\tPrior to being trained by Chris I had done two small triathlons and I wanted to continue down that path. Chris and I connected through social media and I was interested in working with him right away. I was very impressed that he has accomplished full Ironmans and many half Ironman races. Chris is extremely knowledgeable about triathlon training no matter what the distance is. He is always positive and friendly and he's there for all of his athletes. He truly wants to see us achieve our goals. Swimming started out as my biggest challenge but with Chris's guidance and workout plans, it has become a strength of mine. I have improved so much in every sport even placing top three in my age group for every triathlon I did this past year. it's a great feeling to do things you never thought were possible. Both training peaks and trainer road are very easy apps to use and Chris takes work and life events into consideration when planning your workouts for the week. He's very accommodating and understanding. I have been trained by chris for over a year and I have no intention of stopping anytime soon. I have bigger plans and goals and I know he's going to get me across that finish line!!",
