        testimonial: "\tI began working with Chris a few months after my wife Jeanine did. I am a competitive shooter and firearms instructor, and I teach and compete all around the country. I had gotten into endurance style training the year prior after not running for quite a while. After I had seen first-hand Chris personality and attention to detail in scheduling, along with his vast knowledge and experience in the multi-sport world, I decided to sign on with him. I needed a coach that could help improve fitness while helping to manage a busy schedule, as I also train for my sport and teach classes in many states. Our goal was to build fitness while managing the training load properly to keep from burning out and around a schedule that was quite hectic. Originally, we began with just biking and running for the first few months, as those were the most important to my competitive shooting performance. We had quick success after about 6 weeks with increased explosiveness in my running, and noticeable improvements in mental and physical endurance for long days on the range competing and/or teaching. \n\n\tWe added in swimming around the 5th month and have been preparing for next years triathlon and competition season coming up. Being a coach myself, one of the things I personally find highly valuable is the relationship aspect between instructor and student. Aside from his exceptional attention to details, the attitude he displays is one that really will resonate with athletes he works with. He is encouraging and inspirational and will make you feel like you CAN do what youre trying to do! He will also be honest with you when there are opportunities to improve, in a way that doesnt put you down. These are very important, because you need a coach that will tell you what needs to happen to get you where you want to go honestly, and encourage you to get there! Those two are a VERY powerful combination in getting athletes to meet and exceed their potential! Combine that with a lot of real-world experience, and frequent touch points, and you get INEVITABLE success toward your goals. Lastly, you can tell that what Chris does for his athletes isnt for money. He does it because he genuinely wants to see people succeed, and will be there for you when you have questions to share his knowledge and vast expertise. He has life-long clients in my wife and I, and we both highly recommend him!",
